              <h2>æ­å–œä½ çŒœå¯¹äº†ï¼</h2>
              <div class="success-info">
                <div class="success-number">ç­”æ¡ˆæ˜¯: {{ targetNumber }}</div>
                <div class="success-attempts">ç”¨äº† {{ guessCount }} æ¬¡</div>
                <div class="success-rating">{{ getRating() }}</div>
              </div>
              <div class="success-actions">
