          </div>

          <div class="stats-overview">
            <div class="stat-item">
              <div class="stat-number">{{ gameStats.totalGames }}</div>
              <div class="stat-label">æ€»æ¸¸æˆæ•°</div>
            </div>
            <div class="stat-item">
