                v-for="diff in difficulties"
                :key="diff.key"
                class="record-item"
              >
                <div class="record-difficulty">{{ diff.name }}</div>
                <div class="record-attempts">
                  {{ gameStats.bestRecords[diff.key] || 'æœªæŒ‘æˆ˜' }}
                  <span v-if="gameStats.bestRecords[diff.key]">æ¬¡</span>
                </div>
              </div>
            </div>
          </div>
        </el-card>

        <el-card class="tips-card" style="margin-top: 20px;">
          <div slot="header">
