                <div>ä¸­ç­‰: {{ Math.ceil(Math.log2(100)) }}æ¬¡</div>
                <div>å›°éš¾: {{ Math.ceil(Math.log2(1000)) }}æ¬¡</div>
                <div>åœ°ç‹±: {{ Math.ceil(Math.log2(10000)) }}æ¬¡</div>
              </div>
            </div>
