<script>
export default {
  name: 'GuessNumberView',
  metaInfo: {
    title: 'çŒœæ•°å­—æ¸¸æˆ - ç»å…¸æ•°å­—çŒœæµ‹æ¸¸æˆ',
    meta: [
      { name: 'description', content: 'ç»å…¸çš„çŒœæ•°å­—æ¸¸æˆï¼Œç³»ç»Ÿä¼šæç¤ºå¤§å°å¸®ä½ æ‰¾åˆ°ç­”æ¡ˆï¼Œæ”¯æŒå¤šç§éš¾åº¦ï¼Œæä¾›æ™ºèƒ½åˆ†æå’Œæ¸¸æˆç»Ÿè®¡ã€‚' },
      { name: 'keywords', content: 'çŒœæ•°å­—æ¸¸æˆ,æ•°å­—çŒœæµ‹,æ™ºåŠ›æ¸¸æˆ,é€»è¾‘æ¸¸æˆ,åœ¨çº¿æ¸¸æˆ' }
    ]
  },
  data() {
    return {
      difficulties: [
        { key: 'easy', name: 'ç®€å•', min: 1, max: 10, description: '1-10ï¼Œé€‚åˆæ–°æ‰‹' },
        { key: 'medium', name: 'ä¸­ç­‰', min: 1, max: 100, description: '1-100ï¼Œç»å…¸æ¨¡å¼' },
        { key: 'hard', name: 'å›°éš¾', min: 1, max: 1000, description: '1-1000ï¼ŒæŒ‘æˆ˜æ€ç»´' },
        { key: 'hell', name: 'åœ°ç‹±', min: 1, max: 10000, description: '1-10000ï¼Œæé™æŒ‘æˆ˜' }
      ],
      selectedDifficulty: 'medium',
      currentDifficulty: null,
      gameStarted: false,
      gameEnded: false,
      targetNumber: null,
      userGuess: null,
      guessCount: 0,
      guessHistory: [],
      lastHint: null,
      possibleRange: { min: 1, max: 100 },

      gameStats: {
        totalGames: 0,
        totalWins: 0,
        totalAttempts: 0,
        bestRecords: {}
      }
    }
  },
  computed: {
    isValidGuess() {
      return this.userGuess >= this.currentDifficulty.min &&
             this.userGuess <= this.currentDifficulty.max &&
             !isNaN(this.userGuess)
    },

    possibleNumbers() {
      return this.possibleRange.max - this.possibleRange.min + 1
    },

    successRate() {
      if (this.gameStats.totalGames === 0) return 0
      return Math.round((this.gameStats.totalWins / this.gameStats.totalGames) * 100)
    },

    averageAttempts() {
      if (this.gameStats.totalWins === 0) return 0
      return Math.round(this.gameStats.totalAttempts / this.gameStats.totalWins * 10) / 10
    }
  },

  mounted() {
    this.loadGameStats()
  },

  methods: {
    startGame(difficulty) {
      this.currentDifficulty = difficulty
      this.selectedDifficulty = difficulty.key
      this.gameStarted = true
      this.gameEnded = false
      this.targetNumber = this.generateRandomNumber(difficulty.min, difficulty.max)
      this.userGuess = null
      this.guessCount = 0
      this.guessHistory = []
      this.lastHint = null
      this.possibleRange = { min: difficulty.min, max: difficulty.max }

      console.log('Target number:', this.targetNumber) // è°ƒè¯•ç”¨ï¼Œå®é™…å‘å¸ƒæ—¶åº”åˆ é™¤
    },

    generateRandomNumber(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min
    },

    makeGuess() {
      if (!this.isValidGuess) {
        this.$message.warning('è¯·è¾“å…¥æœ‰æ•ˆçš„æ•°å­—')
        return
      }

      this.guessCount++
      const guess = parseInt(this.userGuess)

      let result, hint, hintType, hintIcon

      if (guess === this.targetNumber) {
        result = 'correct'
        hint = 'æ­å–œï¼çŒœå¯¹äº†ï¼'
        hintType = 'success'
        hintIcon = 'el-icon-success'
        this.gameEnded = true
        this.updateGameStats(true)
      } else if (guess < this.targetNumber) {
        result = 'too-small'
        hint = 'å¤ªå°äº†ï¼Œå†å¤§ä¸€ç‚¹'
        hintType = 'warning'
        hintIcon = 'el-icon-arrow-up'
        this.possibleRange.min = Math.max(this.possibleRange.min, guess + 1)
      } else {
        result = 'too-big'
        hint = 'å¤ªå¤§äº†ï¼Œå†å°ä¸€ç‚¹'
        hintType = 'warning'
        hintIcon = 'el-icon-arrow-down'
        this.possibleRange.max = Math.min(this.possibleRange.max, guess - 1)
      }

      // æ·»åŠ åˆ°å†å²è®°å½•
      this.guessHistory.push({
        number: guess,
        hint: hint,
        result: result
      })

      // è®¾ç½®æç¤º
      this.lastHint = {
        message: hint,
        type: hintType,
        icon: hintIcon
      }

      // æ¸…ç©ºè¾“å…¥
      this.userGuess = null
    },

    newGame() {
      this.gameStarted = false
      this.gameEnded = false
      this.currentDifficulty = null
      this.targetNumber = null
      this.userGuess = null
      this.guessCount = 0
      this.guessHistory = []
      this.lastHint = null
      this.possibleRange = { min: 1, max: 100 }
    },

    changeDifficulty() {
      this.newGame()
    },

    getBestRecord() {
      if (!this.currentDifficulty) return 'æ— '
      return this.gameStats.bestRecords[this.currentDifficulty.key] || 'æ— '
    },

    getRating() {
      const theoreticalMin = Math.ceil(Math.log2(this.currentDifficulty.max - this.currentDifficulty.min + 1))

      if (this.guessCount <= theoreticalMin) {
        return 'ğŸ† å®Œç¾ï¼ç†è®ºæœ€ä¼˜è§£'
      } else if (this.guessCount <= theoreticalMin + 2) {
        return 'ğŸ¥‡ ä¼˜ç§€ï¼æ¥è¿‘æœ€ä¼˜'
      } else if (this.guessCount <= theoreticalMin + 5) {
        return 'ğŸ¥ˆ è‰¯å¥½ï¼è¿˜ä¸é”™'
      } else if (this.guessCount <= theoreticalMin + 10) {
        return 'ğŸ¥‰ ä¸€èˆ¬ï¼Œè¿˜æœ‰è¿›æ­¥ç©ºé—´'
      } else {
        return 'ğŸ’ª ç»§ç»­åŠªåŠ›ï¼'
      }
    },

    getStrategy() {
      if (this.guessHistory.length === 0) {
        return 'å»ºè®®ä»ä¸­é—´å€¼å¼€å§‹'
      }

      const middle = Math.floor((this.possibleRange.min + this.possibleRange.max) / 2)
      return `å»ºè®®çŒœæµ‹ ${middle}`
    },

    updateGameStats(won) {
      this.gameStats.totalGames++

      if (won) {
        this.gameStats.totalWins++
        this.gameStats.totalAttempts += this.guessCount

        // æ›´æ–°æœ€ä½³è®°å½•
        const currentBest = this.gameStats.bestRecords[this.currentDifficulty.key]
        if (!currentBest || this.guessCount < currentBest) {
          this.gameStats.bestRecords[this.currentDifficulty.key] = this.guessCount
        }
      }

      this.saveGameStats()
    },

    loadGameStats() {
      try {
        const saved = localStorage.getItem('guessNumberGameStats')
        if (saved) {
          this.gameStats = { ...this.gameStats, ...JSON.parse(saved) }
        }
      } catch (error) {
        console.error('Failed to load game stats:', error)
      }
    },

    saveGameStats() {
      try {
        localStorage.setItem('guessNumberGameStats', JSON.stringify(this.gameStats))
      } catch (error) {
        console.error('Failed to save game stats:', error)
      }
    }
  }
}
</script>

